<template>
  <div>
    <my-header></my-header>
    <!-- 中间板块开始 -->
    <div
      class="billContainer"
      infinite-scroll-distance="1"
      v-infinite-scroll="loadData"
      infinite-scroll-disabled="busy"
    >
      <div class="title" v-text="title"></div>
      <!-- 选项卡开始 -->
      <mt-navbar v-model="active">
        <mt-tab-item id="1">出借信息列表</mt-tab-item>
        <mt-tab-item id="2">本月账单列表</mt-tab-item>
      </mt-navbar>
      <!-- 选项卡结束 -->
      <!-- 面板开始 -->
      <mt-tab-container v-model="active">
        <mt-tab-container-item id="1" class="billList">
          <!-- 出借列表开始 -->
          <!-- <div
            class="list_item"
            v-for="(item, j) in 10"
            :key="j"
            @touchend="toggleItem(j)"
          > -->
          <div
            class="list_item"
            v-for="(item, j) in loanList"
            :key="j"
            @click="toggleItem(j)"
          >
            <div class="title">
              月月盈{{ j }}<span>HRB00215070800002</span>
              <span
                :class="`${openItem == j ? 'triangle_up' : 'triangle_down'}`"
              ></span>
            </div>
            <table v-show="openItem == j">
              <tr>
                <td>
                  <p>账单日期</p>
                  <p><span>15</span>日</p>
                </td>
                <td>
                  <p>投资期限</p>
                  <p><span>12</span>个月</p>
                </td>
              </tr>
              <tr>
                <td>
                  <p>出借金额</p>
                  <p><span>80000.00</span>元</p>
                </td>
                <td>
                  <p>预期收益</p>
                  <p><span>366.67</span>元</p>
                </td>
              </tr>
              <tr>
                <td>
                  <p>出借日期</p>
                  <p><span>2015-07-08</span></p>
                </td>
                <td>
                  <p>到期日期</p>
                  <p><span>2016-07-07</span></p>
                </td>
              </tr>
            </table>
          </div>

          <!-- 出借列表结束 -->
        </mt-tab-container-item>
        <mt-tab-container-item id="2" class="billList">
          <!-- 账单列表开始 -->
          <div class="title">月月盈<span>HRB00215070800002</span></div>
          <table>
            <tbody>
              <tr>
                <th>账单日</th>
                <th>账单状态</th>
                <th>操 作</th>
              </tr>
              <tr v-for="(v, i) in billList" :key="i">
                <td>2017-05-03</td>
                <td>已出账单</td>
                <td>
                  <router-link to="/">发送邮件{{ v }}</router-link>
                </td>
              </tr>
            </tbody>
          </table>
          <!-- 账单列表结束 -->
        </mt-tab-container-item>
      </mt-tab-container>
      <!-- 面板结束 -->
    </div>
    <!-- 中间板块结束 -->
    <my-footer></my-footer>
  </div>
</template>
<style scoped>
/* 中间内容开始 */

/* 账单列表开始 */
.billList {
  font-size: 1rem;
}
.billList .title,
.billList table {
  width: 100%;
  text-align: center;
}
.billList .title {
  background-color: #7fcaef;
  height: 4.016rem /*98px*/;
  line-height: 4.016rem;
  color: #fff;
  position: relative;
}
.billContainer .triangle_down {
  padding: 0 !important;
  width: 0;
  height: 0;
  border: solid;
  border-color: white transparent transparent;
  border-width: 0.8rem 0.24em;
  position: absolute;
  top: 40%;
  right: 9%;
}
.billContainer .triangle_up {
  padding: 0 !important;
  width: 0;
  height: 0;
  border: solid;
  border-width: 0.8rem 0.24em;
  position: absolute;
  top: 24%;
  right: 9%;
  border-color: transparent transparent white;
}
.billList table {
  border-collapse: collapse;
  color: #818181;
}
.billList table tr {
  height: 3.816rem /*98px*/;
  line-height: 3.816rem;
}
.billList table th,
.billList table td {
  border: 0.1rem solid #dad8d8;
}
.billConbillListtainer .title span {
  padding-left: 1.8rem;
}
.billList table tr th:first-child,
.billList table tr td:first-child {
  border-left: 0;
}
.billList table tr th:last-child,
.billList table tr td:last-child {
  border-right: 0;
}
.billList table tr th {
  color: #3d3a39;
}
.billList table a {
  color: #2879be;
}
.billList table span {
  color: #2281d5;
  font-size: 1.58rem;
}
/* 账单列表结束 */
/* 中间内容结束 */
</style>
<script>
// import myHeader from "../components/Header";

export default {
  data() {
    return {
      active: "2",
      openItem: 0,
      title: "本月账单列表",
      loanList: 7,
      billList: 7,
      busy: false,
      // list: [],
    };
  },
  watch: {
    active() {
      if (this.active == "1") {
        this.title = "出借信息列表";
      } else {
        this.title = "本月账单列表";
      }
    },
  },
  // components: {
  //   myHeader,
  // },
  methods: {
    toggleItem(index) {
      this.openItem = index;
      // this.$set(this.list, index, !this.list[index]);
    },
    loadData() {
      this.busy = true;
      this.$indicator.open({
        text: "加载中，请稍后.....",
        type: "fading-circle",
      });
      setTimeout(() => {
        if (this.active == "1") {
          this.loanList += 2;
        } else {
          this.billList += 2;
        }
        this.busy = false;
        this.$indicator.close();
      }, 1000);
    },
  },
  created() {
    document.documentElement.scrollTop = 0;
  },
};
</script>
